
body{
  background-image: url('./img/bg.jpg');
  height: 100vh;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

header{
  height: 400px;
}
header img{
  width: 200px;
}

.center{
  display: flex;
  align-items: center;
  justify-content: center;
}

.container{
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}
.cards{
  display:grid ;
  grid-template-columns: repeat(4, 1fr);
  gap:1rem;
}
.flip-card {
  cursor: pointer;
  background-color: transparent;
  height: 300px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
}
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}
.flip-card-front {
  background-color: #bbb;
  color: black;
}
.flip-card-back {
  background-color:#333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
}
.flip-card img{
  width:100%;
  height:100%;
  object-fit: cover;
}
.flip-card li{
  list-style: none;
  padding-bottom:10px;
}
@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 500px) {
  .cards {
    grid-template-columns: 1fr;
  }
}
.flip-card h1{
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  padding-bottom: 10px;
}
input[type='text'] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: 0;
  border-radius: 5px;
  width: 60%;
  margin: auto;
}
.search {
  height: 100px;
}